
@font-face {
  font-family: 'Queering';
  src: url('./fonts/Queering-Regular.woff2') format('woff2'),
       url('./fonts/Queering-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Queering';
  src: url('./fonts/Queering-Bold.woff2') format('woff2'),
       url('./fonts/Queering-Bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Queering';
  src: url('./fonts/Queering-Heavy.woff2') format('woff2'),
       url('./fonts/Queering-Heavy.woff') format('woff');
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

body {
  margin: 0;
  font-family: 'Queering', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
